
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import classModule from '../../components/ClassModule';

import recommendSwiper from '../../components/RecommendSwiper';

import tabBar from '../../components/TabBar.vue';
import topics from '../../components/Topics.vue';
function getViewportSize () {
return {
width: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
height: window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
}
}
let SIZE = getViewportSize();
export default {
name:'index',
components:{ Flexbox, FlexboxItem,classModule,tabBar,topics,Toast,recommendSwiper },
data(){
return {
showToast:false,
toastMsg: '',
allWidth:SIZE.width,
curWidth:SIZE.width/1.2,
allHeight:SIZE.height/5,
curHeight:SIZE.height/5,
services:[],
threeServices:[],
next:1
}
},
mounted(){
this.initIntrlligent();
},
methods:{
initIntrlligent(){
let vm = this;
this.$http.post('/implement/query?type=1',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
let tempService = [];
console.log(data.data)









vm.services = data.data;
vm.threeServices = data.data.slice(0,3);
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
}
},(error) => {
console.error(error);
})
},
nextServices(){
if(this.services.length==3){
return ;
}
let max = Math.floor(this.services.length/3),tempArr=[];
if(this.next > max){this.next = 0;}
let next = this.next*3;
if(next!=0 && ((this.services.length-next)%3!=0)){tempArr = this.services;}
this.threeServices = [...this.services,...tempArr].slice(next,next+3);
console.log(this.threeServices)
this.next++;
},
goTo(id){
window.location.href = '/m/topicIndex.html?serviceId='+id+'&_='+(new Date().getTime());
}
}
}



