
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tools from '../lib/tools.js';

export default {
name:'recommendSwiper',
components:{Swiper,SwiperItem,Toast},
data(){
return {
showToast:false,
toastMsg: '',
banners: [],
bannerIndex:0,
swiperOption: {
autoplay: 3000,
setWrapperSize :true,
pagination : '.swiper-pagination',
paginationClickable :true,
mousewheelControl : true,
observeParents:true,
spaceBetween: false,
loop:true,
preventClicks:false,
slidesPerView: 1,
centeredSlides: true,
touchRatio:0.5,
autoplayDisableOnInteraction : false,
}
}
},
created(){
let vm = this;

this.$http.post('/implement/query?type=0',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
let tempBanner = [];
for(var i=0;i<data.data.length;i++){
let item = data.data[i];
tempBanner.push({img:item.imgUrl,link:item.name,title:''});
}
tempBanner && (vm.banners = tempBanner);
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
})
},
methods:{
goTo(url){
console.log('to: ',url);
if(!url){return false;}
sessionStorage.setItem('intrlligentUrl',url);
if(url.indexOf('/goods')==0){
tools.next('/m/equipmentIndex.html?sourceType=0&isBuy=1');
}else if(url.indexOf('/service')==0){
tools.next('/m/serviceIndex.html');
}
},
swiperIndexChange(index){
console.log(index);
this.bannerIndex = index;
},
}
}



