
import tools from '../lib/tools.js';
const FastClick = require('fastclick')
FastClick.attach(document.body);
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name:'classModule',
components:{Flexbox, FlexboxItem},
methods:{
goTo(type){
let url = '';
if(type=='lo'){
return this.goToFamilyDoctor();
}
switch(type){

case 'ch':url='/healthService.html';break;
case 'do':url='/findDoctors.html';break;
case 'or':url='/personalTailor.html';break;
}
window.location.href= '/m'+url + '?=_' +(new Date().getTime());
},
goToFamilyDoctor(){
let vm = this;
let id = localStorage.getItem('md') || tools.getCookie('user_id');//md === userid

this.$http.get('/api/contracte/queryDoctors?managerId='+id).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data && data.data.length>0){
tools.next('/m/myFamilyDoctors.html?managerId='+id);
}else{
tools.next('/m/familyOrg.html');
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
}
}
}



