export default {
    name:'topics',
    data(){
        return {
            services:[],
            showToast:false,
            toastMsg:''
        }
    },
    mounted(){
        let vm = this;
        // swiper banner
        // 0:banner  1:发现  2:phr
        this.$http.post('/implement/query?type=1',{}).then((result) => {
            let data = result.data;
            if(data.successful && data.status==200){
                // let tempService = [];
                // for(var i=0;i<data.data.length;i++){
                //     let item = data.data[i];
                //     if(item.serviceList){
                //         tempService.push({type:1,url:item.name,service:item.serviceList[0],imgUrl:item.imgUrl});
                //     }else if(item.goodsList){
                //         tempService.push({type:0,url:item.name,service:item.goodsList[0],imgUrl:item.imgUrl});
                //     }
                // }
                // tempService && (vm.services = tempService);
                vm.services = data.data
            }else{
                vm.showToast = true;
                vm.toastMsg = data.resultCode.message;
                console.error(data);
            }
        },(error) => {
            console.error(error);
        })
    },
    methods:{
        goTo(id){
            window.location.href = '/m/topicIndex.html?serviceId='+id+'&_='+(new Date().getTime());
        }
    }
}



